import React from 'react';
import { Droppable } from 'react-beautiful-dnd';
import Field from './Field';
import './index.css';

const Column = ( props ) => {
	const columnFields = props.columnFields.map( ( fieldId, index ) => {
		return <Field openModal={props.openModal}  sectionName ={props.sectionName}  allFields={ props.allFields } key={ index } index={ index } colId={props.colId} fieldId={ fieldId } fieldProps={props.fieldProps} />
	});
	return (
		<div className={ 'col-cont pure-u-1 pure-u-md-' + props.size } >
			<div className="column">
			{/* <h1 className='title '> { props.title } </h1> */}
				<Droppable type={props.sectionName} droppableId={props.colId}>
				{ ( provided, snapshot ) => {
					const droppableClasses = snapshot.isDraggingOver ? 'activeColumn' : '';
					return (
						<div className={ droppableClasses + ' droppableColumn'}
							{ ...provided.droppableProps } ref={ provided.innerRef } >
							{ columnFields }
							{ provided.placeholder }
							<div className="view-empty-placeholder" onClick={ ( event ) => props.openModal( event, 'addField', 'Add Field', props.colId, props.sectionName ) } >
								<i className="fas fa-plus"></i>
							</div>
						</div>
						
					)
				}}
				</Droppable>
				{/* <div className="view-empty-placeholder" onClick={ ( event ) => props.openModal( event, 'addField', 'Add Field', props.colId, props.sectionName ) } >
					<i className="fas fa-plus"></i>
				</div> */}
			</div>
			
		</div>
	)
}

export default Column;