import React, { Fragment } from 'react';
import Column from './Column';
import { Droppable } from 'react-beautiful-dnd';
import './index.css';


const Row = ( props ) => {
	const columnsArray = props.rowColumns;
	const sectionName = props.sectionName;
	const rowId = props.rowId;
	const columns = columnsArray.map( ( colId, index ) => {
		return <Column title={props.allColumns[ colId ].title} sectionName={props.sectionName} columnFields={props.allColumns[ colId ].fields} key={index}
			openModal={props.openModal} allFields={props.allFields} fieldProps={props.fieldProps} colId={colId} size={props.allColumns[ colId ].size} />
	} );
	let rowSettings = '';
	if( (props.viewType == 'grid' && props.sectionName =='loop') || props.sectionName !='loop' ) {
		rowSettings =<span>
						<span className="view-layout view-setting-icon" onClick={( event ) => props.openModal( event, 'layout', 'Choose Layout', rowId, props.sectionName )}><i className="fas fa-columns"></i></span>
						<span className="view-row-remove view-setting-icon" onClick={( event ) => props.deleteRow( event, rowId, sectionName )}><i className="fas fa-times"></i></span>
					</span>
	}

	return (
		<Fragment>
			<Droppable droppableId={`row${rowId}`} type="row">
				{( provided ) => {
					return (
						<div ref={provided.innerRef} {...provided.droppableProps} className="row pure-g">
							<div className="rowSettingsPanel">
							{ rowSettings }
							</div>
							<div className="columnPanel">
								{columns}
							</div>
							{provided.placeholder}
						</div>
					)
				}

				}
			</Droppable>
			{ props.viewType == 'table' && props.sectionName =='loop' && <p>Each field will be displayed as a column in the table.</p>}
		</Fragment>
	);
}

export default Row;
