import React from 'react';

const FilterSort = (props) => {
	const viewSettingsProps = props.viewSettingsProps;
	//console.log(viewSettingsProps); return;
	return (
			<div className="pure-form viewInlineSec">
				<label className="">Sort direction</label>
				<select name="direction" onChange={(e)=> viewSettingsProps.saveViewSetting(e, 'sort') } value={viewSettingsProps.viewSettings.sort.direction}>
					<option value="ASC">ASC</option>
					<option value="DESC">DESC</option>
				</select>
			</div>
	)

}

export default FilterSort;